i.venues\" :key=\"item.id\">\n                        <h1>{{ item.name }}</h1>\n                        <!-- <p>\n                            {{ item.address }}, {{ item.location.name }},\n                            {{ item.location.state }}\n                        </p> -->\n                        <p>{{ invi.city }}, {{ invi.state }}</p>\n                    </div>\n                    <h4>{{ invi.startDate | moment(\"dddd, Do\") }}</h4>\n                    <h4>{{ invi.startDate | moment(\"MMMM YYYY hh A\") }}</h4>\n                </div>\n            </div>\n        </div>\n        <FlashMessage :message=\"message\"></FlashMessage>\n    </div>\n</template>\n\n<script>\nimport FlashMessage from \"../../FlashMessage.vue\";\nimport Multiselect from \"vue-multiselect\";\n\nexport default {\n    props: [\"loggedIn\", \"invitation\", \"venues\", \"locations\"],\n    components: {\n        FlashMessage,\n        Multiselect,\n    },\n    data() {\n        return {\n            invi: this.invitation ?? {},\n            //selectedCity: null,\n            states: [],\n            district: [],\n            imageOne: null,\n            imageOnePreview: null,\n            disableSubmit: false,\n            edit: false,\n            message: null,\n            errorsSubmit: null,\n            showForm: true,\n            screenWidth: window.screen.width,\n            show: null,\n        };\n    },\n    methods: {\n        // getStates() {\n        //     var _this = this;\n        //     _this.states = _this.locations.filter(function (item) {\n        //         if (item.parent_id == 1) {\n        //             return { name: item.name, id: item.id };\n        //         }\n        //     });\n        // },\n        // getDistricts(e, bool) {\n        //     let _this = this;\n        //     let val = null;\n        //     if (bool == true) {\n        //         //console.log(e.currentTarget);\n\n        //         val = e.currentTarget.value;\n        //     }\n        /